import React, { useState } from 'react';
import Input from './../input/Input';

const ContactForm = () => {
	const [name, changeName] = useState('');
	const [mail, changeMail] = useState('');

	const pushNotice = () => {
		if (name.length > 5 && mail.length > 5)
			alert('Менеджер свяжется с вами в ближайшее время');
		else alert('Введите коррекное имя и способ связи');
	};

	return (
		<form className='contact_form' action=''>
			<h3 className='contact_form_title'>Используйте форму обратной связи</h3>
			<Input
				value={name}
				changeValue={changeName}
				label='Email/телефон'
				type='text'
				className='contacts-email'
			/>
			<Input
				value={mail}
				changeValue={changeMail}
				label='Имя'
				type='text'
				className='contacts-name'
			/>
			<button
				className='button-orange button-orange-o'
				value='Оставить заявку'
				onClick={e => {
					pushNotice();
					e.preventDefault();
				}}
			>
				Оставить заявку
			</button>
		</form>
	);
};

export default ContactForm;
