import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Datalist from './../../datalist/Datalist';
import revImg from './reverse.svg';
import { StoreContext } from '../PlannerStore';
import GoogleDirectionService from '../../GoogleDirectionService/GoogleDirectionService';
import Tooltip from '@material-ui/core/Tooltip';

const useStylesBootstrap = makeStyles(theme => ({
	arrow: {
		color: 'red'
	},
	tooltip: {
		backgroundColor: 'red'
	}
}));

const BootstrapTooltip = props => {
	const classes = useStylesBootstrap();

	return <Tooltip arrow classes={classes} {...props} />;
};

const SidesInputs = props => {
	let distanceId = false;
	const { store, changeRoute, addNewDistance } = useContext(StoreContext);
	const list = props.allRouts.map(el => el.name);
	const from = store.routs[props.id].name;
	const to = store.routs[props.id + 1].name;

	const swapFromTo = () => {
		const newfrom = {
			id: props.id,
			value: store.routs[props.id + 1].name
		};
		const newto = {
			id: props.id + 1,
			value: store.routs[props.id].name
		};
		changeRoute(newto);
		changeRoute(newfrom);
	};

	let delBut = <></>;

	if (props.id > 0) {
		delBut = (
			<button
				className='button-del'
				onClick={e => props.deleteDirection(e, props.id)}
			/>
		);
	}

	const ChangeFrom = val => {
		props.changeReady(false);
		changeRoute({
			id: props.id,
			value: val
		});
		addDistanceInStoreIfDistanceNull();
	};

	const ChangeTo = val => {
		props.changeReady(false);
		changeRoute({
			id: props.id + 1,
			value: val
		});
		addDistanceInStoreIfDistanceNull();
	};

	const addDistanceInStoreIfDistanceNull = () => {
		const to = store.routs[props.id].name;
		const from = store.routs[props.id + 1].name;

		if (store.distances.length === 0 && to && from && from !== to) {
			const routes = {
				route1: {
					id: store.routs[props.id].id,
					name: store.routs[props.id].name,
					lat: store.routs[props.id].lat,
					lng: store.routs[props.id].lng
				},
				route2: {
					id: store.routs[props.id + 1].id,
					name: store.routs[props.id + 1].name,
					lat: store.routs[props.id + 1].lat,
					lng: store.routs[props.id + 1].lng
				}
			};
			addNewDistance(routes);
		}
	};

	if (to !== from && to && from && store.distances.length !== 0) {
		let iHaveNot = true;
		store.distances.map(el => {
			if (
				(el.route1.name === to && el.route2.name === from) ||
				(el.route1.name === from && el.route2.name === to)
			) {
				iHaveNot = false;
				if (el.distance < 0) distanceId = el;
			}
		});
		if (iHaveNot) {
			const routes = {
				route1: {
					id: store.routs[props.id].id,
					name: store.routs[props.id].name,
					lat: store.routs[props.id].lat,
					lng: store.routs[props.id].lng
				},
				route2: {
					id: store.routs[props.id + 1].id,
					name: store.routs[props.id + 1].name,
					lat: store.routs[props.id + 1].lat,
					lng: store.routs[props.id + 1].lng
				}
			};
			addNewDistance(routes);
		}
	}

	return (
		<>
			<GoogleDirectionService response={distanceId}></GoogleDirectionService>
			<BootstrapTooltip
				title='!!! Необходимо заполнить оба поля'
				placement='top-start'
				open={store.classInpitsNull ? true : false}
			>
				<div
					className={'sides ' + store.classInpitsNull}
					id={`sides_${props.id}`}
				>
					<Datalist
						isDisabled={props.id > 0 ? true : false}
						id={`${props.id}`}
						type='text'
						className={'input-send-from'}
						label='Выберите откуда поедем'
						onkeyup={ChangeFrom}
						value={from}
						list={list}
						listId='points_from'
					/>
					<Datalist
						id={`${props.id}`}
						type='text'
						className={'input-send-to'}
						label='Выберите куда поедем'
						onkeyup={ChangeTo}
						value={to}
						list={list}
						listId='points_from'
					/>
					<div className='sides-buttons'>
						<button
							type='button'
							className='button-reverce'
							onClick={swapFromTo}
						>
							<img src={revImg} alt='сменить' />
						</button>
						{delBut}
					</div>
				</div>
			</BootstrapTooltip>
		</>
	);
};

export default SidesInputs;
