import React, { useState, useContext } from 'react';
import Checkbox from '../checkbox/Checkbox';
import Driver from './driver/Driver';
import sedan from './sedan.svg';
import hatchback from './hatchback.svg';
import minivan from './minivan.svg';
import suv from './suv.svg';
import Spinner from '../spinner/Spinner';
import acdbservice from '../../services/acDatabaseService';
import { Ctx } from '../../store';
import CountSlider from '../countSlider/CountSlider';
import MaterialInput from '../materialInput/MaterialInput';

const DriversFilter = props => {
	const {
		store: { apiUrl, plannerDistance }
	} = useContext(Ctx);

	const [sorter, changeSorter] = useState('popularity');
	const [order, changeOrder] = useState(true);
	const [fromDB, loadfromDB] = useState();

	const [filterSedan, changeFilterSedan] = useState(true);
	const [filterSuv, changeFilterSuv] = useState(true);
	const [filterMinivan, changeFilterMinivan] = useState(true);
	const [filterHatchback, changeFilterHatchback] = useState(true);
	const [filterMinSits, changeFilterMinSits] = useState(3);
	const [filterPetrol, changeFilterPetrol] = useState(true);
	const [filterGas, changeFilterGas] = useState(true);
	const [filterDiesel, changeFilterDiesel] = useState(true);
	const [childSit, changeChildSit] = useState(false);
	const [filterPriceMin, changeFilterPriceMin] = useState(0);
	const [filterPriceMax, changeFilterPriceMax] = useState(0);
	const [filterCurrentMin, changeFilterCurrentMin] = useState(filterPriceMin);
	const [filterCurrentMax, changeFilterCurrentMax] = useState(filterPriceMax);

	const changeFilter = action => {
		switch (action) {
			case 'Газ':
				changeFilterGas(!filterGas);
				break;
			case 'Бензин':
				changeFilterPetrol(!filterPetrol);
				break;
			case 'Дизельное':
				changeFilterDiesel(!filterDiesel);
				break;
			case 'Детское кресло':
				changeChildSit(!childSit);
				break;
			case 'Седан':
				changeFilterSedan(!filterSedan);
				break;
			case 'Внедорожник':
				changeFilterSuv(!filterSuv);
				break;
			case 'Минивен':
				changeFilterMinivan(!filterMinivan);
				break;
			case 'Хэтчбэк':
				changeFilterHatchback(!filterHatchback);
				break;
			default: {
				break;
			}
		}
	};

	if (!fromDB) {
		const dbService = new acdbservice({ url: apiUrl });
		dbService.getDrivers().then(res => loadfromDB(res));
		return <Spinner size='500px' />;
	}

	const sortingEl = sortCase => {
		let newOrder = true;
		if (sorter === sortCase) {
			newOrder = !order;
		}
		changeSorter(sortCase);
		changeOrder(newOrder);
	};
	const dataIsFiltred = fromDB
		.filter(el => (filterMinSits <= el.car.sits ? el : null))
		.filter(el => (!filterSedan && el.car.type === 'Седан' ? null : el))
		.filter(el => (!filterSuv && el.car.type === 'Внедорожник' ? null : el))
		.filter(el => (!filterMinivan && el.car.type === 'Минивэн' ? null : el))
		.filter(el => (!filterHatchback && el.car.type === 'Хэтчбэк' ? null : el))
		.filter(el => (!filterPetrol && el.car.fuel === 'Бензин' ? null : el))
		.filter(el => (!filterGas && el.car.fuel === 'Газ' ? null : el))
		.filter(el => (!filterDiesel && el.car.fuel === 'Дизель' ? null : el))
		.filter(el => (childSit && el.car.child === 0 ? null : el))
		.filter(el =>
			filterCurrentMin <= el.car.price * plannerDistance ? el : null
		)
		.filter(el =>
			filterCurrentMax >= el.car.price * plannerDistance ? el : null
		);

	const setValueInput = data => {
		if (data.type === 'min') changeFilterCurrentMin(data.value);
		else if (data.type === 'max') changeFilterCurrentMax(data.value);
	};

	let data = dataIsFiltred.sort((a, b) => {
		let result;
		switch (sorter) {
			case 'price':
				result = b.car.price - a.car.price;
				break;
			case 'rewiews':
				result = a.reviews.quantity - b.reviews.quantity;
				break;
			case 'sits':
				result = a.car.sits - b.car.sits;
				break;
			case 'popularity':
				result = a.reviews.rating - b.reviews.rating;
				break;
			default:
				result = a.reviews.rating - b.reviews.rating;
		}
		if (result < 0) {
			return 1;
		}
		return -1;
	});

	let maxPrice = 0;
	let minPrice = 0;
	if (fromDB.length > 0) {
		maxPrice = fromDB[0].car.price;
		minPrice = fromDB[0].car.price;
		fromDB.forEach(el => {
			if (minPrice > el.car.price) minPrice = el.car.price;
			if (maxPrice < el.car.price) maxPrice = el.car.price;
		});
	}
	if (filterPriceMin !== minPrice) changeFilterPriceMin(minPrice);
	if (filterPriceMax !== maxPrice) changeFilterPriceMax(maxPrice);

	if (filterCurrentMin === 0 && plannerDistance !== 0) {
		changeFilterCurrentMin(minPrice * plannerDistance);
	}
	if (filterCurrentMax === 0 && plannerDistance !== 0) {
		changeFilterCurrentMax(maxPrice * plannerDistance);
	}

	const renderDrivers = data => {
		let arr = data;

		if (!order) {
			arr.reverse();
		}

		return arr.map((driver, idx) => {
			return <Driver key={idx} distance={plannerDistance} {...driver} />;
		});
	};

	return (
		<>
			<div className='wrapper_container'>
				<h3 className='rout_h3'>Выбор водителя</h3>
				<div className='market_drivers'>
					<div className='select_driver'>
						<div className='sort'>
							<h4 className='sort_title'>Сортировать:</h4>
							<ul className='sort_items'>
								<li
									className={sorter === 'popularity' ? 'active' : ''}
									onClick={() => {
										sortingEl('popularity');
									}}
								>
									по рейтингу
								</li>
								<li
									className={sorter === 'rewiews' ? 'active' : ''}
									onClick={() => {
										sortingEl('rewiews');
									}}
								>
									по количеству отзывов
								</li>

								<li
									className={sorter === 'price' ? 'active' : ''}
									onClick={() => {
										sortingEl('price');
									}}
								>
									по цене
								</li>
								<li
									className={sorter === 'sits' ? 'active' : ''}
									onClick={() => {
										sortingEl('sits');
									}}
								>
									по числу мест в машине
								</li>
							</ul>
						</div>
						<div className='drivers'>{renderDrivers(data)}</div>
					</div>
					<div className='filter'>
						<input className='filter_done' type='checkbox' />
						<h3 className='filter_h3'>Фильтр</h3>
						<div className='filter_invision' />
						<div className='filter_container'>
							<div className='filter_container_block'>
								<h4 className='filter_h4'>Тип машины</h4>
								<div className='filter_cars'>
									<label className='filter_cars_container'>
										Седан
										<input
											onChange={() => {
												changeFilter('Седан');
											}}
											type='checkbox'
											checked={filterSedan}
										/>
										<span className='filter_cars_container_checkmark' />
										<img src={sedan} alt='' />
									</label>
									<label className='filter_cars_container'>
										Внедорожник
										<input
											onChange={() => {
												changeFilter('Внедорожник');
											}}
											type='checkbox'
											checked={filterSuv}
										/>
										<span className='filter_cars_container_checkmark' />
										<img src={suv} alt='' />
									</label>
									<label className='filter_cars_container'>
										Минивен
										<input
											onChange={() => {
												changeFilter('Минивен');
											}}
											type='checkbox'
											checked={filterMinivan}
										/>
										<span className='filter_cars_container_checkmark' />
										<img src={minivan} alt='' />
									</label>
									<label className='filter_cars_container'>
										Хэтчбэк
										<input
											onChange={() => {
												changeFilter('Хэтчбэк');
											}}
											type='checkbox'
											checked={filterHatchback}
										/>
										<span className='filter_cars_container_checkmark' />
										<img src={hatchback} alt='' />
									</label>
								</div>
							</div>
							<div className='filter_container_block'>
								<div className='filter_price'>
									<h4 className='filter_h4'>Цена</h4>
									<CountSlider
										setValueInput={value => {
											setValueInput(value);
										}}
										currentMin={filterCurrentMin}
										currentMax={filterCurrentMax}
										min={filterPriceMin * plannerDistance}
										max={filterPriceMax * plannerDistance}
									/>
									<div className='filter_price-inputs'>
										<MaterialInput
											type='min'
											setValueInput={value => {
												setValueInput(value);
											}}
											current={filterCurrentMin}
											text={filterPriceMin * plannerDistance}
											label='мин. ₽'
										/>
										<span>—</span>
										<MaterialInput
											setValueInput={value => {
												setValueInput(value);
											}}
											current={filterCurrentMax}
											type='max'
											text={filterPriceMax * plannerDistance}
											label='макс. ₽'
										/>
									</div>
								</div>
								<div className='filter_sits'>
									<h4 className='filter_h4'>Минимальное Число мест</h4>
									<select
										id='plannerSitsSelect'
										onChange={() => {
											const select = document.getElementById(
												'plannerSitsSelect'
											);
											changeFilterMinSits(select.value);
										}}
									>
										<option value={3}>от 3х мест</option>
										<option value={4}>от 4х мест</option>
										<option value={5}>от 5ти мест</option>
									</select>
								</div>
							</div>
							<div className='filter_container_block'>
								<div className='filter_fuel'>
									<h4 className='filter_h4'>Топливо</h4>
									<div>
										<Checkbox
											checked={filterPetrol}
											changeFilter={a => {
												changeFilter(a);
											}}
											className='filter_fuel'
											title='Бензин'
										/>
										<Checkbox
											checked={filterDiesel}
											changeFilter={a => {
												changeFilter(a);
											}}
											className='filter_fuel'
											title='Дизельное'
										/>
										<Checkbox
											checked={filterGas}
											changeFilter={a => {
												changeFilter(a);
											}}
											className='filter_fuel'
											title='Газ'
										/>
									</div>
								</div>
								<div className='filter_addition'>
									<h4 className='filter_h4'>Дополнение</h4>
									<div>
										<Checkbox
											changeFilter={a => {
												changeFilter(a);
											}}
											checked={childSit}
											className='additionals'
											title='Детское кресло'
										/>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default DriversFilter;
