import React, { useState } from 'react';
import Slide from './slide/Slide';
import Spinner from '../spinner/Spinner';
import acdbservice from '../../services/acDatabaseService';
import { Ctx } from '../../store';

const ToursSlider = props => {
	const {
		store: { apiUrl }
	} = React.useContext(Ctx);
	const [data, loadData] = React.useState();
	const [activeSlideIndex, changeActiveSlideIndex] = useState(0);

	const renderList = () => {
		return data.map((tour, idx) => {
			return (
				<Slide
					key={Math.random()
						.toString(36)
						.substring(2, 9)}
					tour={tour}
					idx={idx}
				/>
			);
		});
	};

	const nextSlide = l => {
		console.log(l);
		let asi = activeSlideIndex;
		const newSlideIndex = asi + 1 < l - 2 ? asi + 1 : 0;
		changeActiveSlideIndex(newSlideIndex);
	};
	const prevSlide = l => {
		let asi = activeSlideIndex;
		const newSlideIndex = asi - 1 >= 0 ? asi - 1 : l - 3;
		changeActiveSlideIndex(newSlideIndex);
	};

	if (!data) {
		const dbService = new acdbservice({ url: apiUrl });
		dbService.getToursFromCategory(props.id).then(res => loadData(res));
		return <Spinner size='250px' />;
	}

	console.log(data);

	let flength = data.length;
	let disabled = '';
	if (flength <= 3) disabled = 'slide-disabled';

	return (
		<div>
			{' '}
			<div className='tours-slider'>
				<h2 className='tours-title'>{props.name}</h2>
				<div className={`slider-one active-slide-${activeSlideIndex}`}>
					<div className='controllers'>
						<div className='scroller'>
							<div className='scroller-from'>01</div>
							<div className='scroller-to'>02</div>
							<span
								style={{
									left: (100 / (flength * 1.5)) * activeSlideIndex
								}}
							/>
						</div>
						<div className='buttons'>
							<button
								className={'prev-slide ' + disabled}
								onClick={() => prevSlide(flength)}
							/>
							<button
								className={'next-slide ' + disabled}
								onClick={() => nextSlide(flength)}
							/>
						</div>
					</div>
					<div
						className={`slider-one-wrapper`}
						style={{
							transform: `translateX(-${(100 / flength) * activeSlideIndex}%)`
						}}
					>
						{renderList()}
					</div>
				</div>
			</div>
		</div>
	);
};

export default ToursSlider;
