import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import MainMenu from '../mainMenu/MainMenu';
import logo from './logotype.png';

const MainNavbar = props => {
	const [menu, changeMenu] = useState(false);
	const history = useHistory();
	let navbarcolor = 'black';
	let pname = history.location.pathname;
	let navState = pname === '/contacts' ? true : menu;

	const handleScroll = () => {
		let newMenuState = window.pageYOffset > 500;
		changeMenu(newMenuState);
	};

	window.addEventListener('scroll', handleScroll);

	if (pname === '/tour-planner') {
		navbarcolor = 'white';
	} else if (pname === '/about') {
		navbarcolor = 'white';
	} else if (pname === '/contacts') {
		navbarcolor = 'white';
	} else if (pname.includes('/driver/')) {
		navbarcolor = 'white';
	}

	if (menu) {
		navbarcolor = 'white';
	}

	return (
		<header className={`header ${navState ? 'lovered' : ''}`}>
			<nav>
				<input className='menu-btn' type='checkbox' id='menu-btn' />
				<label className={`menu-icon ${navbarcolor}`} htmlFor='menu-btn'>
					<span />
					<span />
					<span />
				</label>
				<Link className='logo' to='/'>
					<img src={logo} alt='Алтайкросс логотип' />
				</Link>
				<MainMenu color={navbarcolor} />
				<div className='tel' />
			</nav>
		</header>
	);
};

export default MainNavbar;
