export default class acdbservice {
	constructor(props) {
		this.___apiBase = props.url;
	}

	getResource = async url => {
		let res = await fetch(`${this.___apiBase}${url}`);
		if (!res.ok) {
			throw new Error(res.status);
		}
		return res.json();
	};

	addResource = async (url, data) => {
		let res = await fetch(`${this.___apiBase}${url}`, {
			method: 'put',
			body: JSON.stringify(data),
			headers: {
				'Content-Type': 'application/json'
			}
		});
		if (!res.ok) {
			throw new Error(res.status);
		}
		return res.json();
	};

	getTours = async () => await this.getResource('/api/tours/6');

	getContacts = async () => {
		const data = await this.getResource('/api/settings/all');
		const tempArray = [
			'contacts_phone',
			'contacts_mail',
			'contacts_vk',
			'contacts_facebook'
		];

		let result = {};

		data.data
			.filter(el => {
				if (tempArray.indexOf(el.name) >= 0) return el;
				return null;
			})
			.map(el => {
				result[el.name] = el;
				return null;
			});

		return result;
	};

	getRouts = async () => {
		return await this.getResource('/api/route/routes/all');
	};

	getCategoryes = async () => {
		return await this.getResource('/api/category/all');
	};

	getToursFromCategory = async id => {
		return await this.getResource('/api/category/getTours/' + id);
	};

	getDrivers = async () => {
		return await this.getResource('/api/driver/showall/driversData');
	};

	getDriver = async id => {
		const driver = {
			driver: await this.getResource('/api/driver/' + id),
			car: await this.getResource('api/driver/car/get_car/' + id),
			carPhoto: await this.getResource('api/carphoto/' + id),
			reviews: await this.getResource('api/review/driver/' + id)
		};
		return driver;
	};

	addNotice = async data => {
		console.log(data);
		await this.addResource('/api/notices/additem', data);
	};
}
