import React from 'react';
import AboutSlider from '../aboutSlider/AboutSlider';
import { Link } from 'react-router-dom';

const SliderSection = props => {
	let infoname = 'nature_info';
	if (props.position) {
		infoname += ' nature_info_right';
	}

	let nature = 'nature';
	if (props.position) {
		nature += ' nature_right';
	}

	return (
		<div>
			<section className={nature}>
				<AboutSlider images={props.images} />
				<div className={infoname}>
					<div className='nature_info_title'>{props.title}</div>
					<div className='nature_info_text'>{props.children}</div>
					{props.button ? (
						<Link to='/tour-planner' className='button-orange'>
							{props.button}
						</Link>
					) : (
						<></>
					)}
				</div>
			</section>
		</div>
	);
};

export default SliderSection;
