import React from 'react';
import ToursSlider from '../../components/tours-slider/ToursSlider';
import { Ctx } from '../../store';
import acdbservice from '../../services/acDatabaseService';
import Spinner from '../../components/spinner/Spinner';

const ToursPage = () => {
	const {
		store: { apiUrl }
	} = React.useContext(Ctx);

	const [data, loadData] = React.useState();

	if (!data) {
		const dbService = new acdbservice({ url: apiUrl });
		dbService.getCategoryes().then(res => loadData(res));
		return <Spinner size='500px' />;
	}

	const content = data.data.map(el => (
		<ToursSlider id={el.id} name={el.name} key={Math.random().toString(36)} />
	));

	return (
		<div className='tours-page'>
			<h1 className='page-title'>Актуальные Туры {new Date().getFullYear()}</h1>
			{content}
		</div>
	);
};

export default ToursPage;
