import React, { useEffect, useState } from 'react';
import Load from './Load.jpg';

const AbouteSlider = props => {
	const [images] = useState([]);
	const [loaded, changeLoaded] = useState(false);
	const [centralImage, changeCentralImage] = useState(1);

	async function created() {
		await getImages();
		await sucessLoad();
	}

	async function getImages() {
		props.images.map((el, idx) => {
			images.push({ id: idx, img: el });
			return null
		});
	}

	async function sucessLoad() {
		changeLoaded(true);
	}

	useEffect(() => {
		created();
	}, []);

	// if (images.length === 3 && loaded === false) ;
	// else console.log(images);

	const toggleMainImage = id => {
		changeCentralImage(id);
	};

	return (
		<div
			className={`about-slider active-slide-${centralImage}`}
			style={{
				transition: 'all 1s ease'
			}}
		>
			<div className={`about-slider-slide slide-0`}>
				<div className={`about-slider-slide-0`} />
			</div>
			<div className={`about-slider-slide slide-1`}>
				<img
					src={!loaded ? Load : images[1].img}
					alt=''
					className={`about-slider-slide-1`}
					onClick={() => toggleMainImage(1)}
				/>
			</div>
			<div className={`about-slider-slide slide-2`}>
				<img
					src={!loaded ? Load : images[2].img}
					alt=''
					className={`about-slider-slide-2`}
					onClick={() => toggleMainImage(2)}
				/>
			</div>
			<div className={`about-slider-slide slide-4`}>
				<img
					src={!loaded ? Load : images[0].img}
					alt=''
					className={`about-slider-slide-3`}
					onClick={() => toggleMainImage(4)}
				/>
			</div>
		</div>
	);
};

export default AbouteSlider;
