import React, { useContext } from 'react';
import 'react-day-picker/lib/style.css';
import DayPickerInput from 'react-day-picker/DayPickerInput';
import { DateUtils } from 'react-day-picker';
import 'react-day-picker/lib/style.css';
import dateFnsFormat from 'date-fns/format';
import dateFnsParse from 'date-fns/parse';
import { StoreContext } from '../planner/PlannerStore';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles } from '@material-ui/core/styles';

const DayPickerCalendar = () => {
	const { store, changeDateTransfer } = useContext(StoreContext);

	const WEEKDAYS_SHORT = {
		ru: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб']
	};

	const useStylesBootstrap = makeStyles(theme => ({
		arrow: {
			color: 'red'
		},
		tooltip: {
			backgroundColor: 'red'
		}
	}));

	const BootstrapTooltip = props => {
		const classes = useStylesBootstrap();

		return <Tooltip arrow classes={classes} {...props} />;
	};
	const MONTHS = {
		ru: [
			'Январь',
			'Февраль',
			'Март',
			'Апрель',
			'Май',
			'Июнь',
			'Июль',
			'Август',
			'Сентябрь',
			'Октябрь',
			'Ноябрь',
			'Декабрь'
		]
	};

	const WEEKDAYS_LONG = {
		ru: [
			'Воскресенье',
			'Понедельник',
			'Вторник',
			'Среда',
			'Четверг',
			'Пятница',
			'Суббота'
		]
	};

	const LABELS = {
		ru: { nextMonth: 'следующий месяц', previousMonth: 'предыдущий месяц' }
	};

	const parseDate = (str, format, locale) => {
		const parsed = dateFnsParse(str, format, new Date(), { locale });
		if (DateUtils.isDate(parsed)) {
			return parsed;
		}
		return undefined;
	};

	const formatDate = (date, format, locale) => {
		return dateFnsFormat(date, format, { locale });
	};

	const FORMAT = 'dd/MM/yyyy';

	const handleDayChange = (selectedDay, modifiers, dayPickerInput) => {
		if (selectedDay) changeDateTransfer(selectedDay.toLocaleDateString());
	};

	let calssEmptyCalendar = '';
	if (store.client.date === '' && store.classInpitsNull !== '')
		calssEmptyCalendar = 'calendarInputEmpty';

	return (
		<div className={'calendarInput ' + calssEmptyCalendar}>
			<BootstrapTooltip
				title='!!! Необходимо заполнить дату'
				placement='top-start'
				open={calssEmptyCalendar ? true : false}
			>
				<DayPickerInput
					value={store.client.date}
					formatDate={formatDate}
					format={FORMAT}
					parseDate={parseDate}
					onDayChange={handleDayChange}
					placeholder={`${dateFnsFormat(new Date(), FORMAT)}`}
					dayPickerProps={{
						months: MONTHS['ru'],
						weekdaysShort: WEEKDAYS_SHORT['ru'],
						weekdaysLong: WEEKDAYS_LONG['ru'],
						firstDayOfWeek: 1,
						labels: LABELS['ru']
					}}
					onTodayButtonClick={(day, modifiers) => console.log('222')}
				/>
			</BootstrapTooltip>
		</div>
	);
};

export default DayPickerCalendar;
