import React from 'react';
import car from './car.svg';
import coins from './coins.svg';
import photo from './photo.svg';
import timer from './timer.svg';

const InfoBlock = () => {
	let profit = [
		{ icon: car, text: 'Стаж вождения от 3х лет. Лицензированные водители.' },
		{ icon: photo, text: 'Живописная природа. Остановки по Вашему желанию.' },
		{ icon: coins, text: 'Отмена заказа за сутки до поездки без штрафа.' },
		{ icon: timer, text: 'Цены ниже рыночных. Оплата после поездки' }
	];
	return (
		<>
			{' '}
			<div className='profits'>
				{profit.map(el => {
					return (
						<div
							className='profit'
							key={Math.random()
								.toString(36)
								.substring(2, 9)}
						>
							<div className='profit-icon'>
								<img src={el.icon} alt='' />
							</div>
							<div className='profit-text'>{el.text}</div>
						</div>
					);
				})}
			</div>
			<div className='transfer-about'>
				<h1 className='transfer-heading'>Трансфер</h1>
				<div className='transfer-text'>
					<p>
						Не знаете где отдохнуть летом на Алтае в 2019 году? У нас собрана
						широкая база достопремичательнойстей Алтая: Озеро Ая, Телецкое,
						Чемал, Манжерок и другие... Водители местные, знают каждый уголок.
						Подскажут где можно можно вкусно и недорого поесть, где
						переночевать. Вы можете выбрать тур или составить свой маршрут.
					</p>
					<br />
					<p>
						Заберем вас из любой точки и доставим по месту назначения. После
						поездки можете оставить отзыв о работе водителя. Наш трансфер также
						берет детей. Чтобы узнать где лучше отдохнуть, можете ознакомитсья
						со списком мест, в которые может доставить наш трансфер.
					</p>
				</div>
				<div className='button-group page'>
					<div className='button-orange'>Наши водители</div>
					<div className='button-orange button-orange-o'> Туры</div>
				</div>
			</div>
		</>
	);
};

export default InfoBlock;
