import React from 'react';
import { Link } from 'react-router-dom';

const menuPoints = [
	{ title: 'Спланировать тур', to: '/tour-planner' },
	{ title: 'Туры', to: '/tours' },
	{ title: 'О нас', to: '/about' },
	{ title: 'Контакты', to: '/contacts' }
];

const MainMenu = props => {
	let uncheckMenu = () => {
		document.getElementById('menu-btn').checked = false;
	};
	return (
		<ul className={`menu menu-${props.color}`}>
			{menuPoints.map(el => {
				return (
					<li key={el.title}>
						<Link to={el.to} onClick={() => uncheckMenu()}>
							{el.title}
						</Link>
					</li>
				);
			})}
		</ul>
	);
};

export default MainMenu;
