import React from 'react';

const Input = props => {
	let { id, className, type, label } = props;
	if (props.id) {
		id = `${type}-${className}-${id}`;
	} else {
		id = `${type}-${className}`;
	}
	return (
		<div className={`input ${className || ''}`}>
			<input
				value={props.value}
				type={type}
				id={id}
				placeholder={label}
				onChange={e => {
					props.changeValue(e.target.value);
					if (props.onkeyup) {
						props.onkeyup(e);
					} else {
						return;
					}
				}}
				value={props.value}
				list={props.listId ? `${props.listId}` : ''}
				name={props.listId ? `${props.listId}` : ''}
			/>
			<label htmlFor={id}>{label}</label>
		</div>
	);
};

export default Input;
