import React, { useContext } from 'react';
import {
	GoogleMap,
	LoadScript,
	DirectionsService
} from '@react-google-maps/api';
import { StoreContext } from '../planner/PlannerStore';

const GoogleDirectionService = props => {
	const { updateDistance } = useContext(StoreContext);

	if (!props.response) return <></>;

	const directionsCallback = response => {
		if (response.status === 'OK') {
			const distance = response.routes[0].legs[0].distance.text;
			const id = props.response.id;
			const responce = {
				distance: distance,
				id: id
			};
			updateDistance(responce);
		} else {
			alert(
				'Невозможно просчитать программно маршрут по вашим координатам. Обратитесь к администратору. Тел: +7 *** *** ** **'
			);
		}
	};

	const travelMode = 'DRIVING';

	const response = props.response;

	const origin = {
		lat: parseFloat(response.route1.lat),
		lng: parseFloat(response.route1.lng)
	};

	const destination = {
		lat: parseFloat(response.route2.lat),
		lng: parseFloat(response.route2.lng)
	};

	return (
		<div>
			<LoadScript
				id='script-loader'
				googleMapsApiKey='AIzaSyC-UFPIlFGayWaZ1L-oH2WJsEehhDByY8k'
			>
				<GoogleMap>
					{props.destination !== '' && props.origin !== '' && (
						<DirectionsService
							options={{
								destination: destination,
								origin: origin,
								travelMode: travelMode
							}}
							callback={directionsCallback}
							onLoad={directionsService => {}}
							onUnmount={directionsService => {}}
						/>
					)}
				</GoogleMap>
			</LoadScript>
		</div>
	);
};

export default GoogleDirectionService;
