import React, { useState, useEffect, useContext } from 'react';
import ContactForm from '../../components/contactform/ContactForm';
import { Ctx } from '../../store';
import acdbservice from '../../services/acDatabaseService';

const Contacts = () => {
	const {
		store: { apiUrl }
	} = useContext(Ctx);

	const [data, loadData] = useState();

	const load = 'Загрузка...';

	useEffect(() => {
		const dbService = new acdbservice({ url: apiUrl });
		dbService.getContacts().then(res => loadData(res));
	}, [apiUrl]);

	if (!data) return <>{load}</>;

	console.log(data);

	return (
		<div className='contacts-wrapper'>
			<h1 className='page-title'>Официальный сайт туров по Алтаю</h1>
			<h2 className='page-subtitle'>Как с нами связаться?</h2>
			<div className='contacts'>
				<div className='contacts-list'>
					<h3 className='contacts-list-title'>
						Воспользуйтесь контактными данными
					</h3>
					<ul>
						<li>
							<span>Телефон:</span>
							<span>{data.contacts_phone.text}</span>
						</li>
						<li>
							<span>Почта:</span>
							<span>{data.contacts_mail.text}</span>
						</li>
						<li>
							<span>vk:</span>
							<span>{data.contacts_vk.text}</span>
						</li>
						<li>
							<span>facebook:</span>
							<span>{data.contacts_facebook.text}</span>
						</li>
					</ul>
				</div>
				<div>ИЛИ</div>
				<ContactForm />
			</div>
		</div>
	);
};

export default Contacts;
