import React from 'react';
import { Link } from 'react-router-dom';

const MainPage = () => {
	return (
		<>
			<h1 className='main-title'>Отдых на алтае</h1>
			<div className='main-subtitle'>Начинается с клика</div>
			<Link to='/tour-planner' className='button'>
				Спланировать тур
			</Link>
		</>
	);
};

export default MainPage;
