import React from 'react';

const initialState = {
	apiUrl: 'http://185.148.82.157/',
	plannerDistance: 0
};

const Ctx = React.createContext({ store: initialState });

const reduser = (store, action) => {
	switch (action.type) {
		case 'IncA':
			return { ...store, a: action.payload };
		case 'changePlannerDistance':
			return { ...store, plannerDistance: action.payload };
		default:
			return store;
	}
};

const Store = props => {
	const [store, dispatch] = React.useReducer(reduser, initialState);

	const events = {
		incA: a => dispatch({ type: 'IncA', payload: store.a + a }),
		changePlannerDistance: distance =>
			dispatch({ type: 'changePlannerDistance', payload: distance })
	};

	return (
		<Ctx.Provider value={{ ...events, store }}>{props.children}</Ctx.Provider>
	);
};

export { Store, Ctx };
