import React from 'react';
import { Ctx } from './store';
import MainNavbar from './components/mainNavbar/MainNavbar';
//import style-code
import './styles/index.sass';
//import Pages
import AboutePage from './pages/aboutPage/AboutPage';
import Contacts from './pages/contacts/Contacts';
import DriverPage from './pages/driverPage/DriverPage';
import MainPage from './pages/mainPage/MainPage';
import NotFound from './pages/notfound/NotFound';
import TourPlanner from './pages/tourPlanner/TourPlanner';
import ToursPage from './pages/toursPage/ToursPage';
// import AboutSlider from './components/aboutSlider/AboutSlider';

import { Route, Switch, useHistory } from 'react-router-dom';
import Spinner from './components/spinner/Spinner';
import TestPage from './pages/testPage/TestPage';

const App = () => {
	const { store } = React.useContext(Ctx);
	const history = useHistory();
	if (!store) return <Spinner size='600px' />;

	let container = 'container';

	let pname = history.location.pathname;
	if (pname === '/') {
		container += ' main-page';
	} else if (pname === '/tours') {
		container += ' tours-page-bg';
	}
	return (
		<>
			<MainNavbar />
			<div className={container}>
				<Switch>
					<Route path='/' component={MainPage} exact />
					<Route path='/tour-planner/' component={TourPlanner} exact />
					<Route path='/tours' component={ToursPage} exact />
					<Route path='/contacts' component={Contacts} exact />
					<Route path='/about' component={AboutePage} exact />
					<Route path='/driver/:id' component={DriverPage} exact />
					<Route path='/testPage' component={TestPage} exact />
					<Route path='/' component={NotFound} />
				</Switch>
			</div>
		</>
	);
};

export default App;
