import React, { useContext } from 'react';
import './formInput.sass';
import { StoreContext } from '../planner/PlannerStore';
import InputMask from 'react-input-mask';

const FormInput = props => {
	const { store } = useContext(StoreContext);

	let classState = props.state;

	const handleChange = e => {
		const result = {
			type: props.type,
			value: e.target.value
		};

		props.setDataValue(result);
	};

	let mask = '********************';
	let input = '';
	switch (props.type) {
		case 'phone':
			mask = '8-999-999-99-99';
			if (store.client.phone.length === 15) classState = 'inputContainer-true';
			break;
		case 'date':
			if (store.client.date.length === 10) classState = 'inputContainer-true';
			mask = '99/99/9999';
			break;
		case 'mail':
			if (store.client.mail.includes('@') && store.client.mail.includes('.'))
				classState = 'inputContainer-true';
			input = (
				<input
					onChange={handleChange}
					className={classState}
					style={{ width: props.width }}
					type='text'
					placeholder={props.placeholder}
					value={store.client[props.type]}
				/>
			);
			break;
		case 'name':
			if (store.client.name.length >= 6) classState = 'inputContainer-true';
			input = (
				<input
					onChange={handleChange}
					className={classState}
					style={{ width: props.width }}
					type='text'
					placeholder={props.placeholder}
					value={store.client[props.type]}
				/>
			);
			break;
		case 'time':
			if (store.client.time.length === 5) classState = 'inputContainer-true';
			mask = '99:99';
			break;
		case 'address':
			if (store.client.address.length >= 8) classState = 'inputContainer-true';
			input = (
				<input
					onChange={handleChange}
					className={classState}
					style={{ width: props.width }}
					type='text'
					placeholder={props.placeholder}
					value={store.client[props.type]}
				/>
			);
			break;
		default:
			console.log(props.type);
	}
	if (input === '')
		input = (
			<InputMask
				onChange={handleChange}
				className={classState}
				style={{ width: props.width }}
				type='text'
				placeholder={props.placeholder}
				value={store.client[props.type]}
				mask={mask}
				maskChar={null}
			/>
		);

	return (
		<>
			<div className='inputContainer'>
				<figure>
					<img src={props.icon} alt='' />
				</figure>
				<div className='inputContainer-content'> {input}</div>
			</div>
		</>
	);
};

export default FormInput;
