import React, { useState, useContext } from 'react';
import greystar from './greystar.svg';
import greenstar from './greenstar.svg';
import sedan from '../sedan.svg';
import hatchback from '../hatchback.svg';
import minivan from '../minivan.svg';
import suv from '../suv.svg';
import { Link } from 'react-router-dom';
import { Ctx } from '../../../store';
import ClientForm from '../../clientForm/ClientForm';
import { StoreContext } from '../../planner/PlannerStore';

const Driver = props => {
	const {
		store: { apiUrl }
	} = React.useContext(Ctx);

	const { store } = useContext(StoreContext);

	const [isModal, changeIsModal] = useState(false);

	const createModal = () => {
		changeIsModal(true);
	};

	const closeForm = () => {
		setTimeout(() => {
			changeIsModal(false);
		}, 200);
	};

	let carType = sedan;
	switch (props.car.type) {
		case 'Хэтчбэк':
			carType = hatchback;
			break;
		case 'Минивен':
			carType = minivan;
			break;
		case 'Внедорожник':
			carType = suv;
			break;
		default:
			carType = sedan;
	}

	let starsarr = [];

	for (let i = 0; i < 5; i++) {
		if (i + 1 < props.reviews.rating) {
			starsarr.push(greenstar);
		} else {
			starsarr.push(greystar);
		}
	}

	let stars = starsarr.map(el => {
		return (
			<img
				src={el}
				key={Math.random()
					.toString(32)
					.substring(2, 9)}
				alt='altaycross-star'
			/>
		);
	});
	const name = props.driver.name + ' ' + props.driver.last_name;
	const route =
		store.routs[0].name + ' — ' + store.routs[store.routs.length - 1].name;

	return (
		<div className='driver'>
			{isModal ? (
				<div className='modal'>
					<div className='modal-background' onClick={closeForm} />
					<div className='modal-text'>
						<ClientForm
							driverID={props.driver.id}
							name={name}
							car={props.car.name}
							route={route}
							price={props.distance * props.car.price}
							closeForm={closeForm}
						/>
					</div>
				</div>
			) : (
				''
			)}
			<figure>
				<img
					className='driver_car-photo'
					src={
						props.car.carphoto.car_photo_front.includes('https://')
							? props.car.carphoto.car_photo_front
							: apiUrl + props.car.carphoto.car_photo_front
					}
					alt=''
				/>
			</figure>

			<div className='driver_info'>
				<Link className='driver_info_driver' to={`/driver/${props.driver.id}`}>
					<figure>
						<img
							className='driver_info_driver-photo'
							src={
								props.driver.photo.includes('https://')
									? props.driver.photo
									: apiUrl + props.driver.photo
							}
							alt=''
						/>
					</figure>

					<div className='driver_info_driver-data'>
						<div>
							<div className='driver_info_driver-data_name'>
								{`${props.driver.name} ${props.driver.last_name}`}
							</div>
							<div className='driver_info_driver-data_rating'>{stars}</div>
							<div className='driver_info_driver-data_reviews'>
								{props.reviews.quantity} отзывов
							</div>
						</div>
						<div className='driver_info_driver-car'>
							<img
								src={carType}
								alt=''
								className='driver_info_driver-car_icon'
							/>
							<span className='driver_info_driver-car_model'>
								{props.car.type}
							</span>
						</div>
					</div>
				</Link>
				<div className='driver_info_car_name'>
					<span className='driver_info_car-text_title'>Название машины:</span>
					<span className='driver_info_car-text_description'>
						{props.car.name}
					</span>
				</div>
				<div className='driver_info_car'>
					<div className='driver_info_car-text'>
						<ul>
							<li>
								<span className='driver_info_car-text_title'>Число мест:</span>
								<span className='driver_info_car-text_description'>
									{props.car.sits} мест
								</span>
							</li>
							<li>
								<span className='driver_info_car-text_title'>Топливо:</span>
								<span className='driver_info_car-text_description'>
									{props.car.fuel}
								</span>
							</li>
							<li>
								<span className='driver_info_car-text_title'>
									Детское кресло:
								</span>
								<span className='driver_info_car-text_description'>
									{props.car.child === 1 ? 'Есть' : 'Нет'}
								</span>
							</li>
							<li>
								<span className='driver_info_car-text_title'>Цена:</span>
								<span className='driver_info_car-text_description'>
									{props.distance * props.car.price} р.
								</span>
							</li>
						</ul>
					</div>
					<button
						className='button-orange driver_info_car-button'
						onClick={createModal}
					>
						Заказать
					</button>
				</div>
			</div>
		</div>
	);
};

export default Driver;
