import React from 'react';

const Checkbox = props => {
	return (
		<label className={`checkbox ${props.className}`}>
			{props.title}
			<input
				onChange={() => {
					props.changeFilter(props.title);
				}}
				checked={props.checked}
				type='checkbox'
			/>
			<span />
		</label>
	);
};

export default Checkbox;
