/* eslint react/no-multi-comp:0, no-console:0, react/prop-types:0 */
import 'rc-tabs/assets/index.css';
import React, { useState, useContext } from 'react';
import Tabs, { TabPane } from 'rc-tabs';
import TabContent from 'rc-tabs/lib/SwipeableTabContent';
import ScrollableInkTabBar from 'rc-tabs/lib/ScrollableInkTabBar';
import './clientForm.sass';
import car from './car.svg';
import driver from './user-tie.svg';
import coins from './coins.svg';
import mapMarker from './map-marker.svg';
import man from './Vector.svg';
import mail from './mail.svg';
import phone from './phone.svg';
import calendar from './calendar.svg';
import watch from './watch.svg';
import map from './map.svg';
import FormInput from '../formInput/FormInput';
import { StoreContext } from '../planner/PlannerStore';
import acdbservice from '../../services/acDatabaseService';
import { Ctx } from '../../store';

const ClientForm = props => {
	const {
		store: { apiUrl }
	} = useContext(Ctx);

	const { store, changeClientData } = useContext(StoreContext);
	const [validName, changeValidName] = useState('');
	const [validMail, changeValidMail] = useState('');
	const [validPhone, changeValidPhone] = useState('');
	const [validDate, changeValidDate] = useState('');
	const [validTime, changeValidTime] = useState('');
	const [validAddress, changeValidAddress] = useState('');
	const [validPersonal, changeValidPersonal] = useState(false);

	const getIp = async () => {
		let res = await fetch(`https://jsonip.com/`)
			.then(response => response.json())
			.then(data => data);
		return res.ip + '';
	};

	let title = 'заголовок';
	let next = '2';
	let buttons = <></>;
	let inputs = <></>;
	const [id, changeID] = useState(0);

	const handleInputChange = data => {
		changeClientData(data);
	};

	const date = new Date();

	const dateNotice =
		date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear();

	const addNotice = () => {
		const dbService = new acdbservice({ url: apiUrl });
		const driverID = props.driverID ? props.driverID : 0;
		let transferRoute = '';
		store.routs.forEach(el => (transferRoute += el.name + '/'));
		const newNotice = {
			client_name: store.client.name,
			client_mail: store.client.mail,
			client_phone: store.client.phone,
			client_address: store.client.address,
			transfer_date: store.client.date,
			transfer_time: store.client.time,
			transfer_price: props.price,
			transfer_route: transferRoute,
			driver_name: props.name,
			driver_id: driverID,
			notice_created: dateNotice,
			status: 'ОБРАБОТКА'
		};
		alert('Запрос успешно создан, менеджер свяжется с вами в ближайшее время');
		// console.log(newNotice);
		dbService.addNotice(newNotice);
	};

	if (id === 0) {
		title = 'Контактная информация';
		next = '2';
		inputs = (
			<>
				<FormInput
					state={validName}
					setDataValue={handleInputChange}
					type={'name'}
					width='300px'
					placeholder='Имя Отчество'
					icon={man}
				/>
				<FormInput
					state={validMail}
					setDataValue={handleInputChange}
					type='mail'
					width='300px'
					placeholder='Почта'
					icon={mail}
				/>
				<FormInput
					state={validPhone}
					setDataValue={handleInputChange}
					type='phone'
					width='300px'
					placeholder='Телефон'
					icon={phone}
				/>
			</>
		);
		buttons = (
			<div className='clientForm-form--date_buttons'>
				<button
					className='clientForm-form--date_buttons--prime'
					onClick={() => {
						let check = true;
						if (store.client.name.length < 6) {
							changeValidName('inputContainer-error');
							check = false;
						}
						if (
							!store.client.mail.includes('@') &&
							!store.client.mail.includes('.')
						) {
							changeValidMail('inputContainer-error');
							check = false;
						}
						if (store.client.phone.length !== 15) {
							changeValidPhone('inputContainer-error');
							check = false;
						}
						if (check) {
							changeActiveKey(next);
							changeID(1);
						}
					}}
				>
					Далее
				</button>
			</div>
		);
	}
	if (id === 1) {
		title = 'Отправление';
		next = '1';
		inputs = (
			<>
				<FormInput
					state={validDate}
					setDataValue={handleInputChange}
					type='date'
					width='300px'
					placeholder='Дата'
					icon={calendar}
				/>
				<FormInput
					state={validTime}
					setDataValue={handleInputChange}
					type='time'
					width='300px'
					placeholder='Время 20:30'
					icon={watch}
				/>
				<FormInput
					state={validAddress}
					setDataValue={handleInputChange}
					type='address'
					width='300px'
					placeholder='Город, улица'
					icon={map}
				/>
				<div className='check-personal-data'>
					<input
						checked={!validPersonal}
						onChange={() => {
							changeValidPersonal(!validPersonal);
							console.log(validPersonal);
						}}
						type='checkbox'
					/>
					<span>Я согласен с обработкой персональных данных</span>
				</div>
			</>
		);
		buttons = (
			<div className='clientForm-form--date_buttons'>
				<button
					className='clientForm-form--date_buttons--second'
					onClick={() => {
						changeActiveKey(next);
						changeID(0);
					}}
				>
					Назад
				</button>
				<button
					className='clientForm-form--date_buttons--prime'
					onClick={() => {
						let check = true;

						if (store.client.date.length < 10) {
							console.log('date');
							changeValidDate('inputContainer-error');
							check = false;
						}
						if (store.client.time.length < 5) {
							console.log('time');
							changeValidTime('inputContainer-error');
							check = false;
						}
						if (store.client.address.length < 8) {
							console.log('address');
							changeValidAddress('inputContainer-error');
							check = false;
						}
						if (validPersonal) {
							check = false;
						} else {
							alert(
								'Для продолжения необходимо ваше согласие на обработку персональных данных.'
							);
						}
						if (check) {
							console.log('done');
							addNotice();
						}
					}}
				>
					Заказать
				</button>
			</div>
		);
	}

	const [activeKey, changeActiveKey] = useState('1');

	const onChange = key => {
		console.log(`onChange ${key}`);
		changeActiveKey(key);
	};

	const onTabClick = key => {
		console.log(`onTabClick ${key}`);
		if (key === activeKey) {
			changeActiveKey('');
		}
	};

	return (
		<div className='clientForm'>
			<div className='clientForm-order'>
				<div className='clientForm-item'>
					<figure>
						<img src={driver} alt='' />
					</figure>
					<div className='clientForm-item--data'>
						<span className='clientForm-item--data_header'>Водитель</span>
						<span className='clientForm-item--data_description'>
							{props.name}
						</span>
					</div>
				</div>
				<div className='clientForm-item'>
					<figure>
						<img src={car} alt='' />
					</figure>
					<div className='clientForm-item--data'>
						<span className='clientForm-item--data_header'>Машина</span>
						<span className='clientForm-item--data_description'>
							{props.car}
						</span>
					</div>
				</div>
				<div className='clientForm-item'>
					<figure>
						<img src={mapMarker} alt='' />
					</figure>
					<div className='clientForm-item--data'>
						<span className='clientForm-item--data_header'>Маршрут</span>
						<span className='clientForm-item--data_description'>
							{props.route}
						</span>
					</div>
				</div>
				<div className='clientForm-item'>
					<figure>
						<img src={coins} alt='' />
					</figure>
					<div className='clientForm-item--data'>
						<span className='clientForm-item--data_header'>Цена</span>
						<span className='clientForm-item--data_description'>
							{props.price + 'р.'}
						</span>
					</div>
				</div>
			</div>
			<div className='clientForm-form'>
				<button
					className='clientForm-form--date_close'
					onClick={props.closeForm}
				>
					<div></div>
					<div></div>
				</button>
				<Tabs
					defaultActiveKey={'1'}
					renderTabBar={() => <ScrollableInkTabBar onTabClick={onTabClick} />}
					renderTabContent={() => <TabContent />}
					activeKey={activeKey}
					onChange={onChange}
				>
					<TabPane tab={`1`} key='1'>
						<div className='clientForm-form--date'>
							<span>{title}</span>
							{inputs}
							{buttons}
						</div>
					</TabPane>
					<TabPane tab={`2`} key='2'>
						<div className='clientForm-form--date'>
							<span>{title}</span>
							{inputs}
							{buttons}
						</div>
					</TabPane>
				</Tabs>
			</div>
		</div>
	);
};

export default ClientForm;
