import React, { useContext } from 'react';
import Banner from './../../components/banner/Banner';
import DriversFilter from './../../components/driverFilter/DriversFilter';
import InfoBlock from './InfoBlock';
import { StoreContext } from '../../components/planner/PlannerStore';

const TourPlanner = () => {
	const { store, changeClassInpitsNull } = useContext(StoreContext);
	const [isFilterReady, changeReady] = React.useState(false);

	const toggleReadyFilter = () => {
		if (store.routs[0].name && store.routs[1].name) {
			let bannerPos = document.getElementById('banner');
			window.scrollTo({
				behavior: 'smooth',
				top: bannerPos ? bannerPos.offsetTop + bannerPos.offsetHeight : 0
			});
			changeReady(true);
		} else changeClassInpitsNull('input-send-from-empty');
	};

	return (
		<>
			<Banner
				toggleFunc={() => {
					toggleReadyFilter();
				}}
				changeReady={changeReady}
			/>
			{isFilterReady ? <DriversFilter /> : <InfoBlock />}
		</>
	);
};

export default TourPlanner;
