import React from 'react';
import time from './time.svg';

const Slide = props => {
	return (
		<div className={`tours-slider-slide slide-${props.idx}`}>
			<figure>
				<img src={props.tour.image} alt={`altay-cross-${props.tour.name}`} />
			</figure>
			<div className='tours-slider-slide-text'>
				<div className='tours-slider-slide-text-title'>{props.tour.name}</div>
				<div className='tours-slider-slide-text-time'>
					<img src={time} alt='' />
					{props.tour.time}
				</div>
			</div>
		</div>
	);
};

export default Slide;
