import React from 'react';
import Select from 'react-select';

const DropdownIndicator = props => {
	return <></>;
};

const Datalist = props => {
	const options = props.list.map(el => {
		return { value: el, label: el };
	});

	const handleChange = newValue => {
		props.onkeyup(newValue.value);
	};

	const classDisabled = props.isDisabled ? ' calculator-select--disabled' : '';

	return (
		<div className={'calculator-select' + classDisabled}>
			<Select
				isDisabled={props.isDisabled}
				id={props.className + '_' + props.id}
				className={props.className + ' calculator-select--item'}
				options={options}
				placeholder={props.label}
				components={{ DropdownIndicator }}
				onChange={handleChange}
				value={props.value ? { value: props.value, label: props.value } : ''}
			/>
		</div>
	);
};

export default Datalist;
