import React, { useContext } from 'react';
import acdbservice from '../../services/acDatabaseService';
import { Ctx } from '../../store';

const initialState = {
	allRouts: [],
	routs: [
		{
			id: 0,
			name: '',
			lat: null,
			lng: null
		},
		{
			id: 1,
			name: '',
			lat: null,
			lng: null
		}
	],
	distances: [],
	classInpitsNull: '',
	client: {
		name: '',
		mail: '',
		phone: '',
		date: '',
		time: '',
		address: ''
	}
};

const StoreContext = React.createContext({ store: initialState });

const reduser = (store, action) => {
	switch (action.type) {
		case 'addItem': {
			return { ...store, routs: [...store.routs, action.payload] };
		}
		case 'changeRoute': {
			initialState.allRouts.map(el => {
				if (el.name === action.payload.value) {
					let tempArray = store.routs;
					tempArray[action.payload.id].name = action.payload.value;
					tempArray[action.payload.id].lat = el.latitude;
					tempArray[action.payload.id].lng = el.longitude;
					return { ...store, routs: [...tempArray] };
				}
			});
			return { ...store };
		}
		case 'newRoute': {
			const newRoute = {
				id: store.routs.length,
				name: '',
				lat: null,
				lng: null
			};
			return { ...store, routs: [...store.routs, newRoute] };
		}
		case 'delRoute': {
			let i = 0;
			let tempArray = store.routs
				.filter(el => {
					if (el.id - 1 !== action.payload) {
						return el;
					}
				})
				.map(el => {
					el.id = i;
					i++;
					return el;
				});
			return { ...store, routs: [...tempArray] };
		}
		case 'addNewDistance': {
			const tempArray = {
				id: store.distances.length,
				route1: action.payload.route1,
				route2: action.payload.route2,
				distance: -1
			};
			return { ...store, distances: [...store.distances, tempArray] };
		}
		case 'updateDistance': {
			const distance = action.payload.distance.slice(0, -3);
			store.distances[action.payload.id].distance = distance;
			return { ...store };
		}
		case 'changeClassInpitsNull': {
			return { ...store, classInpitsNull: action.payload };
		}
		case 'changeDateTransfer': {
			store.client.date = action.payload;
			return { ...store };
		}
		case 'changeClientData': {
			store.client[action.payload.type] = action.payload.value;
			return { ...store };
		}
		default:
			return store;
	}
};

const StorePlanner = props => {
	const [data, loadData] = React.useState();
	const {
		store: { apiUrl }
	} = useContext(Ctx);

	const getRoutes = () => {
		const dbService = new acdbservice({ url: apiUrl });
		dbService.getRouts().then(res => loadData(res.data));
	};

	const [store, dispatch] = React.useReducer(reduser, initialState);

	if (!data) {
		getRoutes();
		return <></>;
	}

	data.map(el => {
		const tempAray = [...store.allRouts, el];
		store.allRouts = tempAray;
	});

	const events = {
		addItem: element => dispatch({ type: 'addItem', payload: element }),
		changeRoute: routeName =>
			dispatch({ type: 'changeRoute', payload: routeName }),
		newRoute: (el = '') => dispatch({ type: 'newRoute', payload: el }),
		delRoute: id => dispatch({ type: 'delRoute', payload: id }),
		addNewDistance: routes =>
			dispatch({ type: 'addNewDistance', payload: routes }),
		updateDistance: answer =>
			dispatch({ type: 'updateDistance', payload: answer }),
		changeClassInpitsNull: textClass =>
			dispatch({ type: 'changeClassInpitsNull', payload: textClass }),
		changeDateTransfer: date =>
			dispatch({ type: 'changeDateTransfer', payload: date }),
		changeClientData: data =>
			dispatch({ type: 'changeClientData', payload: data })
	};

	return (
		<StoreContext.Provider value={{ ...events, store }}>
			{props.children}
		</StoreContext.Provider>
	);
};

export { StorePlanner, StoreContext };
