import React, { useState, useEffect, useContext } from 'react';
import SidesInputs from './sides-inputs/SidesInputs';
import { StoreContext } from './PlannerStore';
import Spinner from '../spinner/Spinner';
import { Ctx } from '../../store';
import DayPickerCalendar from '../calendar/DayPickerCalendar';

const Planner = props => {
	const {
		store,
		newRoute,
		delRoute,
		changeClassInpitsNull,
		changeDateTransfer
	} = useContext(StoreContext);

	const {
		store: { plannerDistance },
		changePlannerDistance
	} = React.useContext(Ctx);

	const [sidesCounter, changeSidesCounter] = useState(0);
	const sides = store.routs
		.map(el => {
			if (el.id !== store.routs.length - 1) return el.id;
		})
		.filter(el => {
			if (el || el === 0) return 1;
		});

	const addDirection = e => {
		e.preventDefault();
		newRoute();
		let newCouter = sidesCounter + 1;
		changeSidesCounter(newCouter);
	};

	const deleteDerection = (e, id, dist) => {
		e.preventDefault();
		let newCouter = sidesCounter - 1;
		changeSidesCounter(newCouter);
		delRoute(id);
	};

	if (!store.allRouts) return <Spinner size='300px' />;

	let distance = 0;
	let last = false;
	if (store.distances.length !== 0)
		store.routs.forEach(elRoute => {
			if (last) {
				store.distances.forEach(elDistance => {
					if (
						elDistance.distance > 0 &&
						((elRoute.name === elDistance.route1.name &&
							last.name === elDistance.route2.name) ||
							(elRoute.name === elDistance.route2.name &&
								last.name === elDistance.route1.name))
					)
						distance = distance + parseFloat(elDistance.distance);
				});
				last = elRoute;
			} else {
				last = elRoute;
			}
		});

	if (plannerDistance !== distance) changePlannerDistance(distance);

	if (
		store.routs[0].name &&
		store.routs[1].name &&
		store.classInpitsNull !== ''
	)
		changeClassInpitsNull('');

	if (store.client.date === '') {
		let today = new Date();
		let dd = String(today.getDate()).padStart(2, '0');
		let mm = String(today.getMonth() + 1).padStart(2, '0');
		let yyyy = today.getFullYear();
		today = mm + '/' + dd + '/' + yyyy;
		changeDateTransfer(today);
	}

	return (
		<div className='planner-window'>
			<form action=''>
				<div className='title'>Путешествие по Алтаю</div>
				<div className='container-date' placeholder='дата'>
					<DayPickerCalendar />
				</div>
				{sides.map(el => {
					return (
						<SidesInputs
							key={Math.random().toString(36)}
							changeReady={props.changeReady}
							id={el}
							deleteDirection={deleteDerection}
							allRouts={store.allRouts}
						/>
					);
				})}
				<div className='distance-block'>
					<p>Расстояние, которое вы проедете: {plannerDistance} км.</p>
					<p>
						Средняя стоимость вашего маршрута: {plannerDistance * 73} рублей
					</p>
				</div>
				<div className='button-group'>
					<button
						className='buttons-planner buttons-planner-o'
						onClick={e => {
							addDirection(e);
						}}
					>
						Направление
					</button>
					<button
						className='buttons-planner'
						onClick={e => {
							e.preventDefault();
							if (props.toggleFunc) {
								props.toggleFunc();
							}
						}}
					>
						Спланировать тур
					</button>
					<button
						className='buttons-planner buttons-planner-o'
						onClick={e => e.preventDefault()}
					>
						Все места
					</button>
				</div>
			</form>
		</div>
	);
};

export default Planner;
